<!--
   开发者：い 狂奔的蜗牛
   说明：类似于安卓的吐司信息提示
   版本：V 0.0.1
  -->

<!--html-->
<template>
  <div class="sharewx">
    <transition name="fade">
      <div class="wxjoylayer" v-show="isShow" @click="hide">
        <span v-html="tips"></span>
      </div>
    </transition>
  </div>

</template>

<!--script-->
<script type="text/ecmascript-6">
  export default {
    created(){
      let that = this;
      // 更新数据 关闭弹窗
      window.addEventListener("popstate", function (e) {
        that.isShow = false;
      });
    },
    data(){
      return {
        isShow: false,
        tips: `点击右上角“...”，<br>发送给朋友或分享到朋友圈吧！`
      }
    },
    methods: {
      show(tips){
        if (tips)
          this.tips = tips;
        this.isShow = true;
        this.addLocation();
      },
      hide(){
        //操作浏览器返回 会触发浏览器popstate
        if (location.hash === "#target=share") {
          history.back();
        }
      },
      addLocation() {
        if (window.history && window.history.pushState) {
          window.history.pushState("target", "没有用", "#target=share");
        } else {
          console.log("当前浏览器版本较低，不支持该功能")
        }
      }
    }
  }
</script>

<!--css-->
<style lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  .sharewx
    .wxjoylayer
      background: url("data:image/png;base64,") no-repeat 100% 10px #333
      background-size: 140px auto
      width: 100%
      height: 100%
      opacity .9
      position: fixed
      top: 0
      left: 0
      right: 0
      bottom: 0
      overflow: hidden
      z-index: 2147483646
      backdrop-filter blur(10px)
      transition all 0.3s
      &.fade-enter-active, &.fade-leave-active
        opacity .9
      &.fade-enter, &.fade-leave-active
        opacity 0
      & > span
        padding: 90px 0 0 15px
        font-size: 14px
        line-height: 26px
        color: #fff
        display: block
        overflow: hidden
</style>



