<!--
   开发者：い 狂奔的蜗牛
   说明：cellbox  带有下一步的菜单项
   版本：V 0.0.1
  -->

<!--html-->
<template>
  <div class="cell-box border-1px"
  :style="{height:height,'line-height':height}">
    <div class="cell-box-title">{{title}}</div>
    <div class="cell-box-des"><div v-if="des">{{des}}</div></div>
  </div>

</template>

<!--script-->
<script type="text/ecmascript-6">
  export default {
    props: {
      title: {
        type: String,
        default: "标题"
      },
      des: {
        type: String,
        default: ""
      },
      height: {
        type: String,
        default: "48px"
      }
    }
  }

</script>

<!--css-->
<style lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  ::-webkit-scrollbar
    width 0
    height 0

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5)
    .border-1px
      &::after
        -webkit-transform scaleY(0.7)
        transform scaleY(0.7)
      &::before
        -webkit-transform scaleY(0.7)
        transform scaleY(0.7)

  @media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2)
    .border-1px
      &::after
        -webkit-transform scaleY(0.5)
        transform scaleY(0.5)
      &::before
        -webkit-transform scaleY(0.5)
        transform scaleY(0.5)

    .cell-box
      display flex
      position relative
      height 48px
      line-height 48px
      &::after
        position absolute
        left 0
        bottom 0
        display block
        width 100%
        border-bottom 1px solid rgba(0, 0, 0, .1)
        content ''
      .cell-box-title
          flex 1
          overflow hidden
          text-overflow ellipsis
          white-space nowrap
          padding-left 5px
      .cell-box-des
        width 100px
        flex 0 0 100px
        & > div
          width 71px
          font-size 12px
          overflow hidden
          color #999
          box-sizing border-box
          text-overflow ellipsis
          white-space nowrap
        &::after
          content ""
          position absolute
          right 5px
          top 50%
          margin-top -7px
          display inline-block
          width 14px
          height 14px
          background url("data:image/png;base64,")  no-repeat center
          -webkit-background-size: cover
          background-size:cover
</style>



