<!--
   开发者：い 狂奔的蜗牛
   说明：类似于安卓的吐司信息提示
   版本：V 0.0.1
  -->

<!--html-->
<template>
  <div class="icontoast">
    <transition name="fade">
      <div v-show="isShow" class="icontoast-wrapper">
        <i class="icon" :class="state"></i>
        <div class="text">{{text}}</div>
      </div>
    </transition>
  </div>

</template>

<!--script-->
<script type="text/ecmascript-6">
  export default {
    data(){
      return {
        text: "",
        state: "succeed",// failed
        isShow: false,
        timer: undefined,
      }

    },
    props: {
      displayDuration: {//吐司显示时长，默认2秒
        type: Number,
        default: 2400
      }
    },
    methods: {
      // 显示土司
      show(text, state){
        this.text = text;
        if (state) {
          this.state = state;
        }
        // 清除计时器
        if (this.timer) {
          clearTimeout(this.timer);
        }
        this.isShow = true;
        let that = this;
        //displayDuration毫秒后消失
        this.timer = setTimeout(function () {
          that.isShow = false;
        }, this.displayDuration);
      }
    }
  }

</script>

<!--css-->
<style lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  .icontoast
    .icontoast-wrapper
      position fixed
      min-width 110px
      height 90px
      left 50%
      top 50%
      transform translate(-50%, -50%);
      margin auto
      backdrop-filter blur(10px)
      background rgba(0, 0, 0, .8)
      border-radius 6px
      transition all .6s
      z-index 2147483647
      &.fade-enter-active, &.fade-leave-active
        opacity 1
      &.fade-enter, &.fade-leave-active
        opacity 0
      .icon
        display block
        width 38px
        height 38px
        margin 12px auto
        &.succeed
          background url("data:image/png;base64,") no-repeat center
          background-size: contain
        &.failed
          background url("data:image/png;base64,") no-repeat center
          background-size: contain
      .text
        display block
        width 100%
        padding 0 10px
        text-align center
        line-height 14px
        font-size 14px
        overflow hidden
        text-overflow ellipsis
        white-space nowrap
        box-sizing border-box
</style>



