<!--
   开发者：い 狂奔的蜗牛
   说明：数字键盘
   版本：V 0.0.1
  -->

<!--html-->
<template>
  <div class="number-keyboard">
    <transition
      enter-active-class="animated-keyboard slideInUp-keyboard fadeIn-keyboard"
      leave-active-class="animated-keyboard slideOutDown-keyboard  fadeOut-keyboard"
    >
      <div class="number-keyboard-content-wrapper  border-1px" v-show="isShow">
        <div class="close-keyboard border-1px" @click="hide()">
          <i class="pic"></i>
        </div>
        <ul class="border1px">
          <li class="border-w1px" @click="clickHandle($event)">1</li>
          <li class="border-w1px" @click="clickHandle($event)">2</li>
          <li @click="clickHandle($event)">3</li>
        </ul>
        <ul class="border1px">
          <li class="border-w1px" @click="clickHandle($event)">4</li>
          <li class="border-w1px" @click="clickHandle($event)">5</li>
          <li @click="clickHandle($event)">6</li>
        </ul>
        <ul class="border1px">
          <li class="border-w1px" @click="clickHandle($event)">7</li>
          <li class="border-w1px" @click="clickHandle($event)">8</li>
          <li @click="clickHandle($event)">9</li>
        </ul>
        <ul>
          <li class="border-w1px" :class="{delete:!decimal}" @click="clickHandle($event)">{{decimal ===true?".":"　"}}
          </li>
          <li class="border-w1px " @click="clickHandle($event)">0</li>
          <li class="delete" @click="clickHandle($event)"><i class='delete-pic'></i></li>
        </ul>

      </div>
    </transition>
  </div>

</template>

<!--script-->
<script type="text/ecmascript-6">
  export default {
    data(){
      return {
        isShow: false,
        isClick: false,
        text: ""
      }
    },
    created(){
      let that = this;
      // 更新数据 关闭弹窗
      window.addEventListener("popstate", function (e) {
        that.isShow = false;
      });
    },
    model: {    // 使用model， 这儿2个属性，prop属性说，我要将msg作为该组件被使用时（此处为aa组件被父组件调用）v-model能取到的值，event说，我emit ‘cc’ 的时候，参数的值就是父组件v-model收到的值。
      prop: 'msg',
      event: 'keyboardValueChanged'
    },
    props: {
      msg: "",
      decimal: {
        type: Boolean,
        default: true
      },
      length: {
        type: Number,
        default:undefined
      }
    },
    methods: {
      show(decimal){
        this.isClick = false;
        this.isShow = true;
        this.addLocation();
      },
      hide(){
        //操作浏览器返回 会触发浏览器popstate
        if (location.hash === "#target=popup") {
          history.back();
        }
      },
      addLocation() {
        if (window.history && window.history.pushState) {
          window.history.pushState("target", "没有用", "#target=popup");
        } else {
          console.log("当前浏览器版本较低，不支持该功能")
        }
      },
      clickHandle(event){
        if (event.target.innerText) {
          if(this.length && this.text.length >= this.length)
            return;
          if (event.target.innerText === ".") {
            if (this.text.indexOf(".") === -1 && this.text.indexOf(".") != 0) {
              this.text += event.target.innerText;
            }
          } else {
            this.text += event.target.innerText.replace("　", "");
          }
        } else {
          if (this.text.length > 0)
            this.text = this.text.substring(0, this.text.length - 1);
        }
      }
    },
    watch: {
      "text"(val, oldVal){
       this.$emit("keyboardValueChanged",val)
      }
    }
  }
</script>

<!--css-->
<style lang="stylus" rel="stylesheet/stylus" type="text/stylus">
  ::-webkit-scrollbar
    width 0
    height 0

  @-webkit-keyframes fadeIn-keyboard
    from
      opacity 0
    to
      opacity 1

  @keyframes fadeIn-keyboard
    from
      opacity 0
    to
      opacity 1

  @-webkit-keyframes fadeOut-keyboard
    from
      opacity 1
    to
      opacity 0

  @keyframes fadeOut-keyboard
    from
      opacity 1
    to
      opacity 0

  @-webkit-keyframes slideInUp-keyboard
    from
      -webkit-transform translate3d(0, 100%, 0)
      transform translate3d(0, 100%, 0)
      visibility visible
    to
      -webkit-transform translate3d(0, 0, 0)
      transform translate3d(0, 0, 0)

  @keyframes slideInUp-keyboard
    from
      -webkit-transform translate3d(0, 100%, 0)
      transform translate3d(0, 100%, 0)
      visibility visible
    to
      -webkit-transform translate3d(0, 0, 0)
      transform translate3d(0, 0, 0)

  @-webkit-keyframes slideOutDown-keyboard
    from
      -webkit-transform translate3d(0, 0, 0)
      transform translate3d(0, 0, 0)
    to
      visibility hidden
      -webkit-transform translate3d(0, 100%, 0)
      transform translate3d(0, 100%, 0)

  @keyframes slideOutDown-keyboard
    from
      -webkit-transform translate3d(0, 0, 0)
      transform translate3d(0, 0, 0)
    to
      visibility hidden
      -webkit-transform translate3d(0, 100%, 0)
      transform translate3d(0, 100%, 0)

  @media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5)
    .border-1px
      &::after
        -webkit-transform scaleY(0.7)
        transform scaleY(0.7)

    .border-w1px
      &::after
        -webkit-transform scaleX(0.7)
        transform scaleX(0.7)

  @media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2)
    .border-1px
      &::after
        -webkit-transform scaleY(0.5)
        transform scaleY(0.5)

    .border-w1px
      &::after
        -webkit-transform scaleX(0.5)
        transform scaleX(0.5)

  /*格式化代码会出问题*/
  borderTop1px()
    &::after
      position absolute
      left 0
      top 0
      display block
      width 100%
      border-top 1px solid #E0E0E0
      content ''

  borderBottom1px()
    position relative
    &::after
      position absolute
      left 0
      bottom 0
      display block
      width 100%
      border-bottom 1px solid #E0E0E0
      content ''

  borderRight1px()
    position relative
    &::after
      position absolute
      top 0
      right 0
      display block
      height 100%
      border-right 1px solid #D0D0D0
      content ''

  .number-keyboard
    .animated-keyboard
      -webkit-animation-duration 0.4s
      animation-duration 0.4s
      -webkit-animation-fill-mode both
      animation-fill-mode both

    .fadeOut-keyboard
      -webkit-animation-name fadeOut-keyboard
      animation-name fadeOut-keyboard

    .fadeIn-keyboard
      -webkit-animation-name fadeIn-keyboard
      animation-name fadeIn-keyboard

    .slideOutDown-keyboard
      -webkit-animation-name slideOutDown-keyboard
      animation-name slideOutDown-keyboard

    .slideInUp-keyboard
      -webkit-animation-name slideInUp-keyboard
      animation-name slideInUp-keyboard

  .number-keyboard
    .number-keyboard-content-wrapper
      position fixed
      left 0
      bottom 0
      width 100%
      background-color transparent
      z-index 2147483647
      borderTop1px()
      .close-keyboard
        display block
        height 30px
        text-align center
        background: #fff
        borderBottom1px()
        .pic
          display inline-block
          margin-top 3px
          width 30px
          height 24px
          background url("data:image/png;base64,")
          -webkit-background-size 30px 24px
          background-size 30px 24px
      & > ul
        display flex
        background: #fff
        height 60px
        line-height 60px
        overflow hidden
        & > li
          flex 1
          text-align center
          font-size 28px
          background: #fff
          touch-callout: none
          -webkit-touch-callout none
          user-select none
          -webkit-user-select none
          &:active
            background: #F7F7F7
          &.delete
            background: #F7F7F7
            .delete-pic
              display inline-block
              margin-top 13px
              width 30px
              height 30px
              background: url("data:image/png;base64,")
              background-repeat no-repeat
              -webkit-background-size contain
              background-size contain
              pointer-events none
        & > li:nth-child(1), & > li:nth-child(2)
          borderRight1px()
      & > ul:nth-child(2), & > ul:nth-child(3), & > ul:nth-child(4)
        borderBottom1px()


</style>



